
export function duration(v) {
  v = v / 1000;
  var day = parseInt(v / (24 * 60 * 60));
  var afterDay = v - day * 24 * 60 * 60;
  var hour = parseInt(afterDay / (60 * 60));
  var afterHour = v - day * 24 * 60 * 60 - hour * 60 * 60;
  var min = parseInt(afterHour / 60);
  var second = v - day * 24 * 60 * 60 - hour * 60 * 60 - min * 60;
  return (
    (day > 0 ? day + "天" : "") +
    (day > 0 || hour > 0 ? hour + "小时" : "") +
    (day > 0 || hour > 0 || min > 0 ? min + "分" : "") +
    (day > 0 || hour > 0 || min > 0 || second > 0 ? second + "秒" : "")
  );
}
export function deepClone(obj) {
  var result,
    oClass = isClass(obj);
  //确定result的类型
  if (oClass === "Object") {
    result = {};
  } else if (oClass === "Array") {
    result = [];
  } else {
    return obj;
  }
  for (var key in obj) {
    var copy = obj[key];
    if (isClass(copy) == "Object") {
      result[key] = deepClone(copy); //递归调用
    } else if (isClass(copy) == "Array") {
      result[key] = deepClone(copy);
    } else {
      result[key] = obj[key];
    }
  }
  return result;
}
export function isClass(o) {
  if (o === null) return "Null";
  if (o === undefined) return "Undefined";
  return Object.prototype.toString.call(o).slice(8, -1);
}

export function compare(key, desc) {
  return function (a, b) {
    return desc ? ~~(a[key] < b[key]) : ~~(a[key] > b[key]);
  };
}

/*
* 频率控制 返回函数连续调用时，fn 执行频率限定为每多少时间执行一次
* @param fn {function}  需要调用的函数
* @param delay  {number}    延迟时间，单位毫秒
* @param immediate  {bool} 给 immediate参数传递false 绑定的函数先执行，而不是delay后后执行。
* @return {function}实际调用函数
*/
export const throttle = function (fn, delay, immediate, debounce) {
  var curr = +new Date(), //当前事件
    last_call = 0,
    last_exec = 0,
    timer = null,
    diff, //时间差
    context, //上下文
    args,
    exec = function () {
      last_exec = curr;
      fn.apply(context, args);
    };
  return function () {
    curr = +new Date();
    (context = this),
      (args = arguments),
      (diff = curr - (debounce ? last_call : last_exec) - delay);
    clearTimeout(timer);
    if (debounce) {
      if (immediate) {
        timer = setTimeout(exec, delay);
      } else if (diff >= 0) {
        exec();
      }
    } else {
      if (diff >= 0) {
        exec();
      } else if (immediate) {
        timer = setTimeout(exec, -diff);
      }
    }
    last_call = curr;
  };
};

export function debounce(fn, delay, immediate) {
  return throttle(fn, delay, immediate, true);
}

export function getCookie(name) {
  var arr,
    reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if ((arr = document.cookie.match(reg))) {
    return decodeURIComponent(arr[2]);
  } else {
    return null;
  }
}
export function getServerCookie(cookie, name) {
  var arr,
    reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
  if ((arr = cookie.match(reg))) {
    return decodeURIComponent(arr[2]);
  } else {
    return null;
  }
}

export function delCookie(name) {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(name);
  if (cval != null) {
    document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
  }
}

export function setCookie(name, value, time) {
  var exp = new Date();
  exp.setTime(exp.getTime() + time);
  document.cookie =
    name + "=" + encodeURIComponent(value) + ";expires=" + exp.toGMTString();
}

