export function matchXSS(str) {
  return !!str.match(/[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im);
}

export function isPositiveNum(s) {
  return /^[0-9]*[1-9][0-9]*$/.test(s);
}

export function formatPrice(p, type) {
  if (p > 1) {
    p = p.toFixed(2);
  } else if (1 >= p && p > 0.009) {
    p = p.toFixed(3);
  } else if (0.009 >= p && p > 0.0009) {
    p = p.toFixed(4);
  } else if (0.0009 >= p && p > 0.00009) {
    p = p.toFixed(5);
  } else if (0.00009 >= p) {
    p = p.toFixed(6);
  } else {
    p = p.toFixed(8);
  }
  return p
}

export function formatToPrice(p) {
  p = p * 1;
  if (p > 100000000) {
    p = p / 100000000;
    p = p.toFixed(2) + "亿";
  } else if (p > 10000) {
    p = p / 10000;
    p = p.toFixed(2) + "万";
  } else {
    p = p.toFixed(2);
  }
  return p;
}

export function toThousands(s, n) {
  n = n > 0 && n <= 20 ? n : 2;
  s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
  var l = s
    .split(".")[0]
    .split("")
    .reverse(),
    r = s.split(".")[1],
    t = "";
  for (let i = 0; i < l.length; i++) {
    t += l[i] + ((i + 1) % 3 == 0 && i + 1 != l.length ? "," : "");
  }
  return (
    t
      .split("")
      .reverse()
      .join("") +
    "." +
    r
  );
}

export function searchParse(search) {
  var resultObj = {};
  // var search = window.location.search;
  if (search && search.length > 1) {
    search = search.substring(1);
    var items = search.split("&");
    for (var index = 0; index < items.length; index++) {
      if (!items[index]) {
        continue;
      }
      var kv = items[index].split("=");
      resultObj[kv[0]] =
        typeof kv[1] === "undefined" ? "" : decodeURIComponent(kv[1]);
    }
  }
  return resultObj;
}
export function searchStringify(data, keys) {
  let objstr = "?";
  keys.forEach(key => {
    objstr += `${key}=${data[key]}&`;
  });
  return objstr;
}
//检测手机 by userAgent
export function isMobile() {
  let u = navigator.userAgent;
  let mobile = !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/ipad|iphone/);
  return mobile;
}
//检测 客户端 视口宽度
export function isSmallDevice() {
  return document.body.getBoundingClientRect().width < 960;
}

//格式时间
export function formatTime(time) {
  let date = new Date(time * 1000);
  date = date.toLocaleDateString() + "  " + date.toTimeString().substring(0, 8);
  return date;
}
//react util
export function createMarkup(content) {
  return {
    __html: content
  };
}
